<template>
  <div class="container">
    <el-row type="flex" class="row-bg" justify="center">
      <span class="txt_title">请选择离店日期</span>
    </el-row>
    <el-row type="flex" justify="center">
      <el-form ref="form" :model="form" label-width="120px">
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :span="9">
            <el-form-item label-width="240px" :disabled="true" label="姓名">
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label-width="240px" :disabled="true" label="原离店日期">
              <el-input v-model="form.date2"> </el-input>
            </el-form-item>
            <el-form-item label-width="240px" :disabled="true" label="续住天数">
              <el-input-number v-model="form.days" @change="handleDaysChange" :min="1"></el-input-number>
            </el-form-item>
            <el-form-item label-width="240px" :disabled="true" label="续住后离店日期">
              <el-input v-model="form.date2_extend"> </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item :disabled="true" label="房号">
              <el-input v-model="form.room_no"> </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="16">
      </el-col>
      <el-col :span="8">
        <router-link :to="{name: 'extension'}">
          <el-button type="primary" class="btn_large">返回</el-button>
        </router-link>
        <router-link :to="{name: 'extension/order'}">
          <el-button type="primary" class="btn_large">下一步</el-button>
        </router-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: '某某人',
        room_no: '203',
        date2: '2018-01-13',
        date2_extend: '2018-01-13',
        days: 1
      }
    }
  },
  methods: {
    handleDaysChange(days) {
      console.log(days);
    },
    onSubmit() {
      this.$message('submit!')
    },
    onCancel() {
      this.$message({
        message: 'cancel!',
        type: 'warning'
      })
    }
  }
}
</script>

<style>
</style>



